import React, { memo, useEffect, useMemo } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { selectProducts } from '../../store/slices/Products/ProductsSlice'
import { fetchUsers } from '../../store/slices/Users/UsersAPI'
import Footer from '../Footer/Footer'
import Location from '../Location/Location'
import ProductItem from '../ProductItem/ProductItem'
import './Delivery.css'



function Delivery() {
	const { products } = useSelector(selectProducts)
	const filteredProds = useMemo(() => {
		return (
			products?.filter(item => Math.floor(Math.random() * item.id) < 0.5)
		)
	}, [products])
	return (
		<>
			<main>
				<div className="big_Container bredndcrumbs">
					<div className="bredndcrumbs_cont">
						<p> :</p>
						<a> /</a>
						<Location />
					</div>
				</div>
				<div className="big_Container delivery">
					<div className="delivery_cont">
						<h1>  </h1>
						<p className="titlep">      KardinalGroup:</p>
						<p>
							     .       .   
							 : , , ,  ,  *  
							1000 
						</p>
						<p className="titlep">   KardinalGroup: </p>
						<p>
							     KardinalGroup   , ,
							 .     , , ,     
							.
						</p>
						<p className="titlep"> .</p>
						<p>
							  KardinalGroup    ,    .
							        
							.
						</p>
						<p className="titlep"> .</p>
						<p>
							  (   )         ,  
							  ,       .
						</p>
						<p style={{ marginBottom: 5 }}>
							     
							KardinalGroup:
						</p>
						<p style={{ marginBottom: 5 }}>  ,  </p>
						<p style={{ marginBottom: 5 }}>
							        
							          <a> 8-962-920-96-96.</a>
						</p>
						<p style={{ marginBottom: 5 }}> .   24   .</p>
					</div>
				</div>
				<div className="big_Container also_order bold">
					<div className="also_order_cont">
						<h2>  </h2>
						<div className="also_order_flex">
							{
								filteredProds?.map(el => (
									<ProductItem
										key={el.id}
										img={el.img}
										title={el.title}
										description={el.description}
										cost={el.cost}
										weight={el.weight}
										id={el.id}
									/>
								))
							}
						</div>
					</div>
				</div>
			</main>
			<Footer />
		</>
	)
}

export default memo(Delivery)   